import * as React from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import Button from "@mui/material/Button";
import { useEffect, useState } from "react";

export default function BasicTextFields(props) {
  const [OID, setOID] = useState(null);
  const [serial_ID, set_serial_ID] = useState(null);
  return (
    <Box
      component="form"
      sx={{
        "& > :not(style)": { m: 1, width: "25ch" },
      }}
      noValidates
      autoComplete="off"
    >
      <TextField
        onChange={(event) => {
          set_serial_ID(event.target.value);
        }}
        id="outlined-basic"
        label="Filter by SERIAL ID"
        variant="outlined"
      />
      <Button
        variant="outlined"
        style={{ height: 55.96 }}
        onClick={() => {
          props.goFilter({
            oid: OID,
            serial_id: serial_ID
          }, 1)
        }}
      >
        GO
      </Button>
    </Box>
  );
}
