import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import axios from "axios";
import { useEffect, useState } from "react";
import ImageMapper from "react-image-mapper";
import Button from "@mui/material/Button";
import Popover from "@mui/material/Popover";
import moment from "moment";

export default function BasicTable(props) {
  let [detections, setDetections] = useState(props.detectionz);

  useEffect(() => {
    setDetections(props.detectionz);
  }, [props.detectionz]);

  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell align="left">Index</TableCell>
            <TableCell align="left">ID</TableCell>
            <TableCell align="center">SERIAL ID</TableCell>
            <TableCell align="left">DATA</TableCell>
            <TableCell align="left">POINTS</TableCell>
            <TableCell align="center">IMAGE</TableCell>
            <TableCell align="left">ACTIONS</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {detections &&
            detections.map((row, index) => (
              <TableRowz
                env={props.env}
                key={`${row.id}`}
                row={row}
                index={index}
              />
            ))}
        </TableBody>
        <Button
          variant="outlined"
          style={{ height: 55.96 }}
          onClick={() => {
            props.loadMore(detections);
          }}
        >
          {" "}
          LOAD MORE
        </Button>
      </Table>
    </TableContainer>
  );
}

const TableRowz = (props) => {
  let row = props.row;
  let crop_object = props.row.payload?.crop_object;
  const [anchorEl, setAnchorEl] = useState(null);
  const handlePopoverOpen = (event) => {
    console.log('event.currentTarget', event.currentTarget);
    setAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = () => {
    setAnchorEl(null);
  };

  const open = Boolean(anchorEl);

  return (
    <TableRow
      key={row.name}
      sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
    >
      <TableCell align="left">{props.index + 1}</TableCell>
      <TableCell align="left">{row.id}</TableCell>
      <TableCell align="center">{row.serial_id}</TableCell>
      <TableCell align="left">
      <p>Type: {row.track_object.class === 0 ? <span style={{ color: 'red' }}>Body</span> : <span style={{ color: 'blue' }}>Head</span>}</p>
        {Object.keys(row.track_object).map((key) => {
          return (
            <p>
              {key}: {row.track_object[key]}
            </p>
          );
        })}
        <p>{`Timestamp: ${row.timestamp}`}</p>
        <p>{`Date: ${new Date(parseInt(row.timestamp)).toISOString()}`}</p>
      </TableCell>
      <TableCell align="left">
        <p>{row.track_object.location[0]}</p>
        <p>{row.track_object.location[1]}</p>
        <p>{row.track_object.location[2]}</p>
        <p>{row.track_object.location[3]}</p>
        
      </TableCell>
      <TableCell
        align="center"
        onMouseLeave={handlePopoverClose}
        aria-owns={open ? "mouse-over-popover" : undefined}
        aria-haspopup="true"
      >
          <ImageMapper
            active={false}
            src={`${row.image_url}`}
            map={{
              name: `${row.timestamp_str}`,
              areas: [
                {
                  name: `1-${row.timestamp_str}`,
                  shape: "rect",
                  coords: [
                    row.track_object.location[0] * 450,
                    row.track_object.location[1] * 253,
                    row.track_object.location[2] * 450,
                    row.track_object.location[3] * 253,
                  ],
                  preFillColor: "rgba(147, 250, 165, 0.1)",
                  strokeColor: "rgba(147, 250, 165, 1)",
                  lineWidth: 3
                }
              ],
            }}
            width={450}
          />
        <Popover
          style={{ backgroundColor: "transparent" }}
          id="mouse-over-popover"
          sx={{
            pointerEvents: "none",
          }}
          open={open}
          anchorEl={anchorEl}
          anchorReference="anchorPosition"
          anchorPosition={{
            top: window.innerHeight / 2,
            left: window.innerWidth / 5,
          }}
          anchorOrigin={{
            vertical: "center",
            horizontal: "center",
          }}
          transformOrigin={{
            vertical: "center",
            horizontal: "center",
          }}
          onClose={handlePopoverClose}
          disableRestoreFocus
        >
          {console.log('row', row.serial_id)}
          <ImageMapper
            active={false}
            src={`${row.image_url}`}
            width={1920}
            map={{
              name: `${row.timestamp_str}`,
              areas: [
                {
                  name: `1-${row.timestamp_str}`,
                  shape: "rect",
                  coords: [
                    row.track_object.location[0] * 1920,
                    row.track_object.location[1] * 1080,
                    row.track_object.location[2] * 1920,
                    row.track_object.location[3] * 1080,
                  ],
                  preFillColor: "rgba(147, 250, 165, 0.1)",
                  strokeColor: "rgba(147, 250, 165, 1)",
                  lineWidth: 3
                }
              ],
            }}
          />
        </Popover>
      </TableCell>
      <TableCell
        onClick={handlePopoverOpen}
      >
        <div style={{ cursor: 'pointer' }}>
         SHOW NOW
        </div>
      </TableCell>
    </TableRow>
  );
};

